import React from "react";
import { Col, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./ElementoDesarrollo.scss";

const ElementoDesarrollo = ({
  title,
  category,
  direccion,
  img,
  orientation,
}) => {
  return (
    <>
      {orientation === "izq" ? (
        <Link to={`/desarrollos/${title}`} className="link_detalle">
          <Row className="row_elementoDes">
            <Col xs={6}>
              <img src={img} alt="" className="img_elemento" />
            </Col>
            <Col xs={6} className="col_dat-elemento izq">
              <div className="div_dat-elemento izq">
                <h1 className="h1_dat-elemento">{title}</h1>
                <h6 className="h6_dat-elemento">{direccion}</h6>
                <h6 className="h6_dat-elemento">{category}</h6>
              </div>
            </Col>
          </Row>
        </Link>
      ) : (
        <Link to={`/desarrollos/${title}`} className="link_detalle">
          <Row className="row_elementoDes">
            <Col xs={6} className="col_dat-elemento der">
              <div className="div_dat-elemento der">
                <h1 className="h1_dat-elemento">{title}</h1>
                <h6 className="h6_dat-elemento">{category}</h6>
                <h6 className="h6_dat-elemento">{direccion}</h6>
              </div>
            </Col>
            <Col xs={6}>
              <img src={img} alt="" className="img_elemento" />
            </Col>
          </Row>
        </Link>
      )}
    </>
  );
};

export default ElementoDesarrollo;
